import { Data, Datum } from '../../../types';
import { SankeyLayoutInputData } from '../../../utils/transform/sankey';

/**
 *  sankey layout 
 * 1. 
 * 2. 
 * @param data
 * @param sourceField
 * @param targetField
 * @param weightField
 */
export function transformDataToSankey(
  data: Data,
  sourceField: string,
  targetField: string,
  weightField: string
): SankeyLayoutInputData {
  if (!Array.isArray(data)) {
    return {
      nodes: [],
      links: [],
    };
  }

  const nodes = [];
  const links = [];

  // TODO 
  // 1.  Map  includesindexOf 
  // 2. link 

  //  sankey layout 
  data.forEach((datum: Datum) => {
    const source = datum[sourceField];
    const target = datum[targetField];
    const weight = datum[weightField];

    // source node
    if (!nodes.includes(source)) {
      nodes.push(source);
    }

    // target node
    if (!nodes.includes(target)) {
      nodes.push(target);
    }

    // links
    links.push({
      source: nodes.indexOf(source),
      target: nodes.indexOf(target),
      value: weight,
    });
  });

  return {
    nodes: nodes.map((name) => ({ name })),
    links,
  };
}
